            code: '320000',
            address: '江苏省'
          },
          {
            code: '360000',
            address: '江西省'
          },
          {
            code: '220000',
            address: '吉林省'
          }
        ],
        'L-S': [
          {
            code: '210000',
            address: '辽宁省'
          },
          {
            code: '150000',
            address: '内蒙古自治区'
          },
          {
            code: '640000',
            address: '宁夏回族自治区'
          },
          {
            code: '630000',
            address: '青海省'
          },
          {
            code: '370000',
            address: '山东省'
          },
          {
            code: '310000',
            address: '上海市'
          },
          {
            code: '140000',
            address: '山西省'
          },
          {
            code: '610000',
            address: '陕西省'
          },
          {
            code: '510000',
            address: '四川省'
          }
        ],
        'T-Z': [
          {
            code: '710000',
            address: '台湾省'
          },
          {
            code: '810000',
            address: '香港特别行政区'
          },
          {
            code: '120000',
            address: '天津市'
          },
          {
            code: '650000',
            address: '新疆维吾尔自治区'
          },
          {
            code: '540000',
            address: '西藏自治区'
          },
          {
            code: '530000',
            address: '云南省'
          },
          {
            code: '330000',
            address: '浙江省'
          }
        ]
      },
      cityData: {},
      countyData: {},
      currProvince: '',
      currCity: '',
      currCounty: ''
    }
  },
  methods: {
    selectItems (i) {
      this.showLevel = i + 1
      this.clickItem = i
    },
    selectProvince (obj) {
      this.currProvince = obj.address
      this.selectAddress.splice(0, this.selectAddress.length)
      this.selectAddress[0] = {
        province: obj.address,
        code: obj.code,
        address: obj.address
      }
      let city = null
      this.cityData = null
      this.countyData = null
      for (let key in cityData) {
        if (obj.code === key) {
          city = cityData[key]
          break
        }
      }
      if (city !== null) {
        this.cityData = city
      }
      if (this.showLevel < this.level) {
        this.showLevel ++
      } else {
        this.showLevel = 0
        this.$emit('finish', this.selectAddress)
      }
    },
    selectCity (city, code) {
      this.currCity = city
      this.selectAddress.splice(1, this.selectAddress.length)
      this.selectAddress[1] = {
        city: city,
        code: code,
        address: city
      }
      let countyData = null
      this.countyData = null
      for (let key in cityData) {
        if (code === key) {
          countyData = cityData[key]
          break
        }
      }
      if (this.showLevel < this.level) {
        this.showLevel ++
      } else {
        this.showLevel = 0
        this.$emit('finish', this.selectAddress)
      }
      if (countyData !== null) {
        this.countyData = countyData
      } else {
        this.showLevel = 0
        this.$emit('finish', this.selectAddress)
      }
    },
    selectCounty (county, code) {
      this.currCounty = county
      this.selectAddress[2] = {
        county: county,
        code: code,
        address: county
      }
      this.showLevel = 0
      this.$emit('finish', this.selectAddress)
    },
    initAddress () {
      let me = this
      me.currProvince = me.province
      let province = null
      for (let key in me.provinceData) {
        let flag = false
        for (let i in me.provinceData[key]) {
          if (me.provinceData[key][i].address === me.province) {
            province = me.provinceData[key][i]
            flag = true
            break
          }
        }
        if (flag) {
          break
        }
      }
      me.selectAddress.splice(0, me.selectAddress.length)
      me.selectAddress.push({
        province: province.address,
        code: province.code,
        address: province.address
      })
      let citys = null
      for (let key in cityData) {
        if (province.code === key) {
          citys = cityData[key]
          break
        }
      }
      let countys = null
      me.cityData = citys
      if (me.level >= 2) {
        me.currCity = me.city
        if (citys !== null) {
          for (let key in citys) {
            if (me.city === citys[key]) {
              me.selectAddress.push({
                city: citys[key],
                code: key,
                address: citys[key]
              })
              break
            }
          }
          for (let key in cityData) {
            if (me.selectAddress[1].code === key) {
              countys = cityData[key]
            }
          }
        }
      }
      if (me.level >= 3) {
        me.currCounty = me.county
        if (countys != null) {
          for (let key in countys) {
            if (me.county === countys[key]) {
              me.selectAddress.push({
                county: countys[key],
                code: key,
                address: countys[key]
              })
              break
            }
          }
        }
      }
      me.countyData = countys
    },
    clickOut () {
      this.showLevel = 0
    }
  },
  created () {},
  mounted () {
    let me = this
    let timeout = setTimeout(() => {
      me.initAddress()
      window.clearTimeout(timeout)
    }, 20)
  }
}
</script>

<style>
  .address_ctx{
    position: relative;
    width: 100%;
  }

  .address_show{
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    background-color: white;
    overflow: hidden;
  }

  .address_show_block{
    flex: 1;
  }

  .address_show_items{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    font-size: 12px;
    overflow: hidden;
    white-space: nowrap;
  }

  .address_selcet{
    background-color: white;
    overflow: hidden;
  }

  .address_selcet_out{
    position: absolute;
    top: 40px;
    left: 0;
    width: 100vw;
    height: calc(100vh - 40px);
    z-index: 8;
    display: flex;
    flex-direction: column;
  }


  .address_selcet_nav{
    background-color: #f0f0f0;
    overflow: hidden;
    height: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
  }

  .address_selcet_nav_item{
    width: fit-content;
    background-color: #f0f0f0;
    height: 40px;
    padding: 0 20px;
    line-height: 40px;
    font-size: 14px;
    box-sizing: border-box;
    border-bottom: 1px solid #c9c9c9;
    border-right: 1px solid #c9c9c9;
  }

  .address_selcet_nav_item_block{
    width: 100%;
    flex: 1;
    box-sizing: border-box;
    user-select: none;
    border-bottom: 1px solid #c9c9c9;
  }

  .address_selcet_nav_item_on{
    border-bottom: 1px solid white;
    background-color: white;
  }

  .address_selcet_ctx{
    width: 100%;
    background-color: white;
    box-sizing: border-box;
    padding: 15px;
  }

  .address_selcet_ctx_item{
    overflow: hidden;
    width: 100%;
  }

  .address_selcet_ctx_province{
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    margin-bottom: 10px;
  }

  .address_selcet_ctx_province_lab{
    padding: 0 10px;
    height: 23px;
    line-height: 23px;
  }

  .address_selcet_items_ctx{
    flex: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .address_selcet_item{
    padding: 0 10px;
    font-size: 13px;
    height: 23px;
    overflow: hidden;
    line-height: 23px;
  }

  .selected{
    background-color: #508aff;
    color: white;
    border-radius: 3px;
  }

  .show_selected{
    background-color: rgba(17, 255, 199, 0.1);
  }


</style>



